<template>
    <div class="admin" v-wechat-title="$route.meta.title">
        <div class="head">
            <!-- <el-row>
                <el-col :span="3" style="text-align: left;border: 1px solid red;">
                    <img src="@/assets/imgs/login-log.fw.png" style="height: 60px; display: block;">
                </el-col>
                <el-col :span="21" style="text-align: right;">
                    <div style="padding-right: 20px;">
                        <el-badge :value="0" class="item">
                            <el-button type="text" @click="onMessage">消息</el-button>
                        </el-badge>
                        <el-button type="text" @click="onMy">我的</el-button>
                        <el-button type="danger" @click="onLogout" plain size="mini">退出</el-button>
                    </div>
                </el-col>
            </el-row> -->
            <div class="head-logo">
                <img src="@/assets/imgs/logo.png">
            </div>
            <div class="head-title">
                协同运营管理系统
            </div>
            <div class="head-icon" style="    height: 36px;
    margin-top: 10px;
    margin-right: 35px;">
                <div style="float: left;width: 36px;height: 36px;border-radius: 18px;background: #F2F2F2;cursor: pointer;">
                    <img src="@/assets/imgs/message.png">
                </div>
                <span style="display: inline-block;float: left;margin:6px 10px;height: 25px;width: 1px;background: #ccc;"></span>
                <div style="float: left;width: 36px;height: 36px;border-radius: 18px;background: #F2F2F2;cursor: pointer;">
                    <el-dropdown trigger="click">
                        <!-- <span class="el-dropdown-link">
                            下拉菜单<i class="el-icon-arrow-down el-icon--right"></i>
                        </span> -->
                        <img src="@/assets/imgs/mine.png" style="line-height: 40px;">
                        <el-dropdown-menu slot="dropdown" style="width: 55px;">
                            <el-dropdown-item style="text-align: center;"><div @click="onMessage">消息</div></el-dropdown-item>
                            <el-dropdown-item style="text-align: center;"><div @click="onMy">我的</div></el-dropdown-item>
                            <el-dropdown-item style="text-align: center;"><div  @click="onLogout">退出</div></el-dropdown-item>
                            
                        </el-dropdown-menu>
                        </el-dropdown>
                </div>
            </div>
        </div>

        <div class="content" :style="{width:width + 'px', height:height - 62 +'px'}">

            <div class="left">
                <div :style="{width:'100%', height:height - 62 +'px', overflow:'auto'}" class="topnav_box">
                    <el-menu
                        @select="onClickMenu"
                        :style="{height:height - 62 +'px',textAlign:'left'}"
                        :default-active="active_uuid"
                        class="dddd">
                        <el-menu-item index="console">控制台</el-menu-item>
                        <el-submenu 
                            v-for="item in left_menus"
                            :key="item.uuid"
                            :index="item.uuid" 
                            >
                            <template slot="title">{{item.name}}</template>
                            
                            <el-menu-item 
                                v-for="citem in item.children"
                                :key="citem.uuid"
                                :index="citem.uuid">{{citem.name}}</el-menu-item>
                        </el-submenu>
                    </el-menu>
                </div>
            </div>
            <div class="right" :style="{width:width - 220 + 'px', height:height - 62 +'px'}">  
                <div :style="{width:width - 220 + 'px', height:height - 62 +'px',overflow:'auto'}">
                    <div class="right-content">
                        <div style="background: #fff;">
                            <router-view />
                        </div>
                    </div>
                </div>
            </div>
                
        </div>
        <!-- 消息列表 -->
        <el-drawer  
            title="消息列表"
            :visible.sync="message_show"
            size="40%">
            <div style="padding-right: 10px;position: relative;">
                <el-table 
                    :data="message_rows"
                    v-loading="loading"
                    element-loading-spinner="el-icon-loading"
                    element-loading-text="拼命加载中"
                    element-loading-background="rgba(0, 0, 0, 0.8)"
                    :highlight-current-row="true"
                    @sort-change="onTagSortChange"
                    @current-change="onTagSelectRow"
                    style="width: 100%"
                    size="mini">
                    
                    <el-table-column type="index" label="#"></el-table-column>
                    <el-table-column prop="conten_tpe" label="内容类型"></el-table-column>
                    <el-table-column prop="content" label="消息内容"></el-table-column>
                    <el-table-column prop="send_time" label="发送时间" :formatter="common.formatDate" sortable></el-table-column>
                    <el-table-column label="操作">
                        <template slot-scope="scope">
                            <el-button
                            size="mini"
                            type="primary"
                            @click="handleDetail(scope.$index, scope.row)">
                                详细
                            </el-button>
                            <el-button
                            size="mini"
                            type="danger"
                            @click="handleDel(scope.$index, scope.row)">
                                删除
                            </el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="page-tag">
                    <el-pagination
                        :current-page.sync="SearchFromData.page_num"
                        @current-change="onPageChange"
                        layout="prev, pager, next, jumper"
                        :total="tag_total">
                    </el-pagination>
                </div>
            </div>
        </el-drawer>
        <!-- 消息详细 -->
        <el-drawer  
            title="消息详细"
            :visible.sync="message_detail"
            size="30%">
            <div style="padding-right: 10px;position: relative;">
                <ul class="admin-ul">
                    <li>
                        <span>内容类型：</span>
                        <span>{{MessageDetail.content_type}}</span>
                    </li>
                    <li>
                        <span>消息内容：</span>
                        <span>{{MessageDetail.content}}</span>
                    </li>
                    <li>
                        <span>消息类型：</span>
                        <span>{{MessageDetail.msg_type}}</span>
                    </li>
                    <li>
                        <span>消息类型关联详细：</span>
                        <span>{{MessageDetail.msg_type_about_detail}}</span>
                    </li>
                    <li>
                        <span>发送类型：</span>
                        <span>{{MessageDetail.send_type}}</span>
                    </li>
                    <li>
                        <span>发送时间：</span>
                        <span>{{MessageDetail.send_time}}</span>
                    </li>
                    <li>
                        <span>发送ip：</span>
                        <span>{{MessageDetail.send_ip}}</span>
                    </li>
                    <li>
                        <span>发送组织：</span>
                        <span>{{MessageDetail.send_group_detail}}</span>
                    </li>
                    <li>
                        <span>发送关联账号：</span>
                        <span>{{MessageDetail.send_about_detail}}</span>
                    </li>
                    <li>
                        <span>接收类型：</span>
                        <span>{{MessageDetail.get_type}}</span>
                    </li>
                    <li>
                        <span>接收关联类型：</span>
                        <span>{{MessageDetail.get_group_detail}}</span>
                    </li>
                    <li>
                        <span>接收关联账户：</span>
                        <span>{{MessageDetail.get_about_detail}}</span>
                    </li>

                    <li>
                        <span>记录添加时间：</span>
                        <span>{{MessageDetail.add_time}}</span>
                    </li>
                    <li>
                        <span>查看次数：</span>
                        <span>{{MessageDetail.loo_num}}</span>
                    </li>
                </ul>
                <span slot="footer">
                    <el-button @click="message_detail = false" size="small">取消</el-button>
                </span>
            </div>
        </el-drawer>
        <!-- 我的 -->
        <el-dialog
        title="我的"
        :visible.sync="isMy"
        width="400px"
        class="my-content">
            <el-form label-width="70px" size="small">
                <el-form-item label="账号：">
                    <el-input v-model="zh"></el-input>
                </el-form-item>
                <el-form-item label="密码：">
                    <el-input v-model="mm" show-password ></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer">
                <el-button @click="isMy = false" size="small">取 消</el-button>
                <el-button type="primary" size="small">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    import Vue from "vue";
    import lime from "@/lime.js";
    import store from "@/store";
    import util from "@/util.js";

    if (!store.state.AdminData) {
        Vue.set(store.state, 'AdminData', {
            zh: '',
            mm: '',

            menus: [],
            left_menus: [],
            right_menus: [],
            active_uuid:'console',
            active_menu: {},
            active_title: '',
            manage_name: '个人中心',
            manage_mobile: '',
            ManageResetForm: {
                name: '',
                old_pwd: '',
                new_pwd: '',
            },
            // 消息列表
            loading: false,
            message_rows: [],
            message_show: false,
            cur_row: null,
            SearchFromData: {
                page_num: 1,
                page_len: 10,
                order_field:'add_time',
                order_sort:'desc',
            },
            tag_total: 0,
            // 详细
            message_detail: false,
            MessageDetail: {

            },
            // 我的
            isMy: false 
        })
    }


    export default {
        data() {
            return store.state.AdminData
        },
        computed:{
            width:() => {
                return store.state.AppData.width;
            },
            height:() => {
                return store.state.AppData.height;
            }
        },
        created() {
            this.initMenuList();
            this.initMessageList();
        },
        methods:{
            /**
             * 获取菜单列表
             */
            initMenuList() {
                lime.req('ManageMenuList', {
                    login_token:lime.cookie_get('login_token')
                }).then(res => {
                    this.menus = res.data;

                    // 左边的菜单处理
                    let _left_menu = [];
                    res.data.forEach(item => {
                        if (item.menu_type < 2){
                            _left_menu.push(item);
                        }
                    })

                    this.left_menus = util.toTree(_left_menu);

                    // 初始化右边菜单列表
                    this.initRightMenu(this.$route.params.menu_uuid);
                })
            },
            // 消息列表
            initMessageList () {
            //     var ws = new WebSocket("ws://47.110.162.90:8383");
            //     // 发送数据
            //     //ws.onopen = function () {}
            //     // 接受数据
            //     ws.onmessage = function (evt) {
            //         console.log(evt.data)
            //     }
            //     // 关闭连接
            //     ws.onclose = function() { 
            //       // 关闭 websocket
            //       //alert("连接已关闭..."); 
            //    };



                lime.req('ManageMsgList', {
                    login_token:lime.cookie_get('login_token'),
                    page_num: this.SearchFromData.page_num,//起始页
                    page_len: this.SearchFromData.page_len,//每页数
                    order_field: this.SearchFromData.order_field,//排序字段
                    order_sort: this.SearchFromData.order_sort,
                }).then(res => {
                    this.message_rows = res.data.rows;
                    this.tag_total = res.data.total;
                })
            },
            // 消息列表
            onMessage () {
                this.message_show = true;
            },
            // 消息列表排序
            onTagSortChange (column, prop, order) {
                this.SearchFromData.order_field = column.prop;
                if (column.order == 'ascending') {
                    this.SearchFromData.order_sort  = 'asc';
                } else {
                    this.SearchFromData.order_sort  = 'desc';
                }
                this.tagInit();
            },
            // 选择消息
            onTagSelectRow (row) {
                this.cur_row = row;
            },
            //分页处理
            onPageChange (page) {
                this.SearchFromData.page_num = page;
                this.initMessageList();
            },
            onMy () {
                this.isMy = true;
            },

            // 消息-详细
            handleDetail (index,row) {
                lime.req('ManageMsgDetail', {
                    uuid: row.uuid,
                    login_token:lime.cookie_get('login_token')
                }).then(res => {
                    if (res.code == 0) {
                        this.MessageDetail = res.data;
                        this.message_detail = true;
                    } else {
                        this.$message.error(res.msg);
                    }
                })
                
            },
            // 获取右边菜单  初始化右边菜单
            initRightMenu(uuid) {
                this.right_menus = [];

                if (uuid != '' && uuid != 'console') {
                    this.menus.forEach(item => {

                        // 处理左边菜单的当前点击或者刷新处理
                        if (item.uuid == uuid) {
                            this.active_menu = item;
                            this.active_title = item.name;
                            this.active_uuid = item.uuid;
                        }

                        
                        if (item.parent_uuid == uuid) {
                            this.right_menus.push(item);
                        }
                    });
                } else {
                    this.active_menu = {},
                    this.active_title = '控制台';
                    this.active_uuid  = 'console';
                }
            },

            onClickMenu(index,indexPath) {
                this.initRightMenu(index);

                console.log(this.active_menu);

                // 内部跳转链接
                if(this.active_menu.font_type == 0){
                    this.$router.push(this.active_menu.font_url + '/' + index);
                } else if (this.active_menu.font_type == 1) {
                    this[this.active_menu.font_url]();
                } else if (this.active_menu.font_type == 2) {
                    window.open(this.active_menu.font_url);
                } else {
                    this.$router.push('/admin/');
                }
            },
            onLogout() {
                //console.log()
                if (lime.cookie_get('login_token') == null) {
                    this.$router.push('/login');
                } else {
                    this.$confirm('确认退出系统?', '提示').then(() => {
                        lime.cookie_set('login_token', null, 0);
                        this.$router.push('/login');
                    })
                }
            }
        }
    }
</script>

<style lang="less">
    .admin{
        .head{
            height: 60px;
            line-height: 40px;
            width:100%;
            overflow: hidden;
            display: flex;
            .head-logo{
                width: 220px;
                background: #0F7BF6;
                img{
                    display: block;
                    margin: 12px auto;
                }
            }
            .head-title{
                flex: 1;
                padding-left: 24px;
                line-height: 60px;
                text-align: left;
            }
            .head-icon{}
        }

        .content {
            width:100%;
            overflow: hidden;
            //border-top:solid 1px #e6e6e6;
        }

        .left{
            width:220px;
            display: inline-block;
            overflow: hidden;
            .el-submenu{
                .is-active{
                    background: #E7F2FE;
                    border-right: 4px solid #0F7BF6;
                }
            }
        }

        .right{
            display: inline-block;
            overflow: hidden;
            text-align: left;
            .right-content{
                padding: 16px;
                background: #f6f7f9;
            }
        }
        .topnav_box::-webkit-scrollbar {
            width: 7px;
            height: 10px;
            background-color: #b5b1b1;
        }

        .topnav_box::-webkit-scrollbar-track {
            -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
            background-color: black;
        }

        .topnav_box::-webkit-scrollbar-thumb {
            -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
            background-color: #b5b1b1;
        }
        .el-badge__content{
            top: 15px;
            right: 60px;
            -webkit-animation-name: cirlceAnimate;
            -webkit-animation-duration: 0.5s;
            -webkit-animation-iteration-count: infinite;
            -webkit-animation-direction: alternate;
            -webkit-animation-timing-function: ease;
            -webkit-animation-play-state: running;

            -moz-animation-name: cirlceAnimate;
            -moz-animation-duration: 0.5s;
            -moz-animation-iteration-count: infinite;
            -moz-animation-direction: alternate;
            -moz-animation-timing-function: ease;
            -moz-animation-play-state: running;

            -ms-animation-name: cirlceAnimate;
            -ms-animation-duration: 0.5s;
            -ms-animation-iteration-count: infinite;
            -ms-animation-direction: alternate;
            -ms-animation-timing-function: ease;
            -ms-animation-play-state: running;

            -o-animation-name: cirlceAnimate;
            -o-animation-duration: 0.5s;
            -o-animation-iteration-count: infinite;
            -o-animation-direction: alternate;
            -o-animation-timing-function: ease;
            -o-animation-play-state: running;
        }
        .el-badge{
            padding-right: 10px;
        }
        @-webkit-keyframes cirlceAnimate{
            0% {opacity: 1;}
            50% {opacity: 0.5;}
            100% {opacity: 0;}
        }
        .page-tag{
            width: 100% !important;
            .el-pagination{
                text-align: right;
            }
        }
        // 详细
        .admin-ul{
            list-style: none;
            li{
                span:nth-of-type(1){
                    width: 150px;
                    text-align: right;
                    display: inline-block;
                }
                span:nth-of-type(2){
                    display: inline-block;
                }
            }
        }
        // 我的
        .my-content{
            .el-dialog__header{
                text-align: left;
            }
        }
    }
    .page{
        bottom: 35px !important;
        right: 50px !important;
    }
    .search-box{
        height: 92px;
        overflow: hidden;
        .el-form{
            height: 33px;
            margin-top: 29px;
            margin-left: 20px;
            overflow: hidden;
            .el-form-item{
                display: inline-block;
            }
        }
    }
    .has-gutter{
        tr{
            th{
                background: #f4f8fe !important;
                padding: 0;
                div{
                    color: #2A2F3B;
                    font-size: 16px;
                    font-weight: 100;
                }
            }
        }
    }
    .el-table .warning-row {
        background: #f4f8fe;
    }
    .el-table .success-row {
        background: #fff;
    }
    
    .el-table__body tr.current-row > td {
    background-color: #cedbeb !important;
    }
    
    /*鼠标移入某行时的背景色*/
    .el-table--enable-row-hover .el-table__body tr:hover > td {
        background-color: #cedbeb;
        // border-top: 1px solid #0f7bf6;
        // border-bottom: 1px solid #0f7bf6;
        // height: 46px;
    }
    
    table th,
    table td {
        height: 48px;
        line-height: 48px;
        border-bottom: 1px solid #fff;
        div{
            font-size: 14px;
        }
    }
    .el-table::before {
        height: 0px;
    }
    table tr{
        height: 48px;
        line-height: 48px;
    }
    .el-dropdown-menu{
        .el-dropdown-menu__item{
            padding: 0 !important;
            button{
                padding: 0 20px !important;
                display: block;
                height: 36px;
                width: 100%;
                span{
                    font-size: 14px;
                }
            }
        }
    }
    .el-dropdown-link{
        color: #409EFF;
    }
    .el-drawer__header span:focus{
        outline: 0 !important;
    }
</style>